.sc-isolated-node .sc-cell .se-expression ::selection {
  background: var(--stencila-green-transparent);
}

.sc-cell .se-show-code {
  width: 34px;
  height: 34px;
  background: #fff;
  border-radius: 0;
  text-align: center;
  /* position: relative; */
}

.sc-cell {
  position: relative;
}

.sc-cell .se-status {
  position: absolute;
  left: -2px;
  top: 0px;
  bottom: 0px;
  width: 2px;
}

.sc-cell .se-status i {
  display: none;
}

.sc-cell .se-show-code .se-status i {
  display: inline;
  font-size: 14px;
  position: relative;
  top: -2px;
  color: white;
}

.sc-cell .se-status-description {
  position: absolute;
  right: 0px;
  top: 10px;
  text-align: right;
  width: 200px;
  font-size: 13px;
  color: #999;
}

.sc-cell .se-show-code .se-toggle-label {
  position: absolute;
  top: 10px;
  left: 40px;
  /* width: 200px; */
  font-size: 13px;
  color: #999;
  text-align: left;
}

.sc-cell .se-show-code .se-status {
  position: relative;
  left: 7px;
  width: 20px;
  height: 20px;
  border-radius: 10px;
}

.sc-cell .se-show-code:hover .se-status {
  opacity: 0.7;
}


.sc-cell .se-show-code > .se-status {
  background: #0788D1;
  color: white;
}

