.sc-cell .se-status-description.sm-ok .se-status-name {
  color: #08b508
}

.sc-cell .se-status-description.sm-error .se-status-name,
.sc-cell .se-status-description.sm-broken .se-status-name,
.sc-cell .se-status-description.sm-failed .se-status-name {
  color: #FF0300;
}

.sc-cell .se-status-description.sm-blocked .se-status-name,
.sc-cell .se-status-description.sm-ready .se-status-name,
.sc-cell .se-status-description.sm-waiting .se-status-name {
  color: #FFAA00;
}

.sc-cell .se-status-description.sm-pending .se-status-name {
  color: #575757;
}


.sc-cell .se-cell-editor-container {
  position: relative;
  background: #F5F5F5;
}

.sc-cell .se-ellipsis {
  position: absolute;
  top: 0px;
  right: 0px;
}

.sc-cell .se-language {
  position: absolute;
  top: 34px;
  right: 9px;
  font-size: 10px;
  color: #888;
  font-family: var(--mono-font-family);
}

/* HACK: overriding button styles */
.sc-cell .se-ellipsis > button {
  border-radius: 0;
  height: 29px;
}

.sc-cell .se-ellipsis .sc-menu {
  position: absolute;
  top: 30px;
  right: 0px;
}

.sc-cell .se-expression {
  font-family: var(--mono-font-family);
  font-size: 13px;
  font-weight: normal;
  color: #737373;
}

.sc-cell .sc-cell-value {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.sc-cell .sc-cell-value img {
  max-width: 100%;
  margin: 0 auto;
  display: block;
}

.sc-cell .sc-cell-value.sm-pending {
  opacity: 0.3;
}

.sc-cell .se-error {
  padding-top: 20px;
  color: #FF0300;
  text-align: center;
  font-size: 12px;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}
